<template>
  <div id="app" @focus="checkFont">
    <SignatureCreator v-if="!mustCheckFont" :isMobile="isMobile"/>
    <div v-if="mustCheckFont">
      <p>Before creating your email signature, you must install the Open Sans font:</p>
      <ol>
        <li><a href="https://files.classyllama.com/bc9abf9a/Open_Sans.zip" download target="_blank" rel="noopener">Download</a> the Open Sans font</li>
        <li>Unzip ​the ​files.</li>
        <li>Open ​the ​“Open_Sans” folder ​now ​unzipped</li>
        <li>Select ​all ​the ​“.ttf” ​files</li>
        <li>Right ​click ​on ​the ​selected ​files ​and ​choose ​“Open”</li>
        <li>When ​opened, click ​“Install ​Font”</li>
      </ol>
    </div>
  </div>
</template>

<script>
import SignatureCreator from './components/SignatureCreator'

export default {
  name: 'app',
  data() {
    return {
      hasFont: this.checkFont()
    }
  },
  components: {
    SignatureCreator
  },
  created() {
    if (this.mustCheckFont) {
      setInterval(this.checkFont.bind(this), 1000)
    }
  },
  computed: {
    mustCheckFont() {
      return !this.hasFont && !this.isMobile;
    },
    isMobile() {
      var checkMobile = {
        webOS: function() {
          return /webOS/i.test(navigator.userAgent);
        },
        Android: function() {
          return /Android/i.test(navigator.userAgent);
        },
        BlackBerry: function() {
          return /BlackBerry/i.test(navigator.userAgent);
        },
        iOS: function() {
          return /iPhone|iPad|iPod/i.test(navigator.userAgent);
        },
        Opera: function() {
          return /Opera Mini/i.test(navigator.userAgent);
        },
        Windows: function() {
          return /IEMobile/i.test(navigator.userAgent);
        },
      };
      var isMobile;
      isMobile = false;
      for (var key in checkMobile) {
        var func = checkMobile[key];
        if (func() === true) {
          isMobile = key;
          break;
        }
      }
      return isMobile;
    },
  },
  methods: {
    checkFont() {
      console.log("%c checking for font...", 'color:#F80')
      this.hasFont = isFontAvailable('Open Sans') || this.isMobile;
      return this.hasFont;
    }
  }
}
</script>

<style>
  #app {
    font-family: 'Open Sans', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    padding: 24px;
    font-size: 16px;
    line-height: 24px;
    color: #777;
  }
  h1 {
    margin: 0 0 32px;
    font-weight: 800;
    color: #333;
    font-size: 48px;
    line-height: 56px;
  }
  a {
    color: #0098F0;
  }
  a:hover {
    text-decoration: none;
  }
  @media screen and (max-width: 500px) {
    #app {
      max-width: 288px;
      padding: 16px;
      margin: 0 auto;
    }
    h1 {
      font-size: 9vw;
      line-height: 1.1;
    }
  }
</style>



